const { createSlice } = require("@reduxjs/toolkit");

const filterSlie = createSlice({
    name: "filter",
    initialState: { value: "" },
    reducers: { onChangeFilter(state, action) {
        state.value = action.payload;
        },
    },
})

export const filterReducer = filterSlie.reducer;
export const { onChangeFilter } = filterSlie.actions;